.dark {
  background-color: #000;
  color: #f2f2f2;
}

.safearea {
  max-width: 87.2%;
  margin-right: auto;
  margin-left: auto;
}

section::before,
section::after {
  content: " ";
  display: table;
}

main {
  min-height: 5000px;
}

.section-dark {
  background-color: #000;
  color: #f2f2f2;
}

.hero__screen {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal;
}
.hero__title {
  font-size: 90px;
  font-weight: 800;
  line-height: 0.875;
  letter-spacing: -0.032em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-transform: uppercase;
  max-width: 250px;
  margin: 0 auto;
}
@media only screen and (min-width: 768px) {
  .hero__title {
    font-size: 120px;
  }
}
@media only screen and (min-width: 992px) {
  .hero__title {
    font-size: 150px;
  }
}
@media only screen and (min-width: 1200px) {
  .hero__title {
    font-size: 180px;
  }
}
.will-change .hero__title {
  will-change: transform, opacity;
}
.hero__container {
  min-height: 300vh;
  overflow: hidden;
}
.hero__scroller {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 100vh;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
}
.hero__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  list-style: none;
  text-align: center;
  padding: 0;
  width: 87.2%;
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
@media only screen and (min-width: 768px) {
  .hero__list {
    max-width: 670px;
  }
}
@media only screen and (min-width: 768px) {
  .hero__list {
    max-width: 800px;
  }
}
.hero__list-item {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.021em;
  line-height: 1.124;
  position: absolute;
  opacity: 0;
  transform: translateY(30px);
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .hero__list-item {
    font-size: 50px;
    font-weight: 700;
    letter-spacing: -0.021em;
    max-width: 670px;
  }
}
@media only screen and (min-width: 992px) {
  .hero__list-item {
    font-size: 64px;
    font-weight: 700;
    letter-spacing: -0.021em;
    max-width: 800px;
  }
}
.will-change .hero__list-item {
  will-change: transform, opacity;
}

.section-board {
  overflow: hidden;
}
.board__excerpt {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #111;
  margin-top: 10vh;
}
.board__excerpt-logo {
  width: 50px;
  height: 50px;
  padding: 10px;
  color: white;
  box-sizing: content-box;
  background-color: #333;
  border-radius: 1rem;
}
.board__excerpt-logo > svg {
  display: block;
  width: inherit;
  height: inherit;
}
@media only screen and (min-width: 992px) {
  .board__excerpt-logo {
    width: 70px;
    height: 70px;
    border-radius: 1.5rem;
  }
}
.board__excerpt-title {
  font-size: 50px;
  font-weight: 700;
  line-height: 53px;
  letter-spacing: -0.032em;
  margin-top: 1rem;
  margin-bottom: 0;
  max-width: 370px;
}
@media only screen and (min-width: 768px) {
  .board__excerpt-title {
    font-size: 64px;
    line-height: 67px;
    max-width: 420px;
  }
}
@media only screen and (min-width: 992px) {
  .board__excerpt-title {
    font-size: 74px;
    line-height: 77px;
    max-width: 570px;
    margin-top: 32px;
  }
}
.board__excerpt-desc {
  font-size: 21px;
  font-weight: 600;
  letter-spacing: -0.032em;
  line-height: 1.475;
  margin-top: 1rem;
  max-width: 330px;
}
@media only screen and (min-width: 768px) {
  .board__excerpt-desc {
    max-width: 420px;
  }
}
@media only screen and (min-width: 992px) {
  .board__excerpt-desc {
    font-size: 24px;
    max-width: 490px;
    margin-top: 28px;
  }
}
.board__excerpt-action {
  display: block;
  margin-top: inherit;
  text-decoration: none;
}
.board__excerpt-logo, .board__excerpt-title, .board__excerpt-desc {
  opacity: 0;
  transform: translateY(60px);
}
.will-change .board__excerpt-logo, .will-change .board__excerpt-title, .will-change .board__excerpt-desc {
  will-change: transform, opacity;
}
.board__ratings {
  height: 500px;
  position: relative;
  margin-top: 32px;
  overflow: hidden;
  transition: opacity 300ms ease-out, transform 300ms ease-out;
}
@keyframes infinite-scroll {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-50%, 0);
  }
}
.board__ratings-list {
  overflow: inherit;
  height: inherit;
  position: absolute;
  list-style: none;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  padding: 0;
  margin: 0 auto;
  top: 0;
  left: 0;
  animation: infinite-scroll 8s linear infinite;
}
@media only screen and (min-width: 992px) {
  .board__ratings-list {
    animation: infinite-scroll 15s linear infinite;
  }
}
.will-change .board__ratings-list {
  animation-play-state: paused;
}
.board__ratings-item {
  flex: 1 0 272px;
  width: 272px;
  height: inherit;
  padding: 8px;
  display: inline-block;
  transform: translateZ(0);
}
@media only screen and (min-width: 768px) {
  .board__ratings-item {
    padding: 12px;
    flex: 1 0 420px;
    width: 420px;
  }
}
@media only screen and (min-width: 992px) {
  .board__ratings-item {
    padding: 12px;
    flex: 1 0 500px;
    width: 500px;
  }
}
.board__ratings-item + li {
  margin-left: 0;
}
.board__ratings-content {
  height: 300px;
}
@media only screen and (min-width: 768px) {
  .board__ratings-content {
    height: 360px;
  }
}
@media only screen and (min-width: 992px) {
  .board__ratings-content {
    height: 420px;
  }
}
.board__ratings.will-change {
  opacity: 0;
  transform: translateY(60px);
}